@import 'variables.scss';

h1, h2, h3, h4 {
  font-family: $primaryFont;
}

.main-container {
  padding-top: 50px;

  .col-lg-8 {
    margin-left: auto;
    margin-right: auto;
    margin-top: -100px;
  }
}

.tile {
  padding: 40px;

  font-size: 16px;
  line-height: 28px;

  background-color: #FFFFFF;

  box-shadow: 0px 2px 5px 1px #BDC3C7;

  @media (max-width: 576px) {
    padding: 30px;
  }
}

.bottom10 {
  margin-bottom: 10px;
}

.bottom20 {
  margin-bottom: 20px;
}

.bottom30 {
  margin-bottom: 30px;
}
